<template>
    <div id="refund">
        <!-- 已退款 -->
        <div class="refund_list" v-for="i in list" :key="i.id">
            <OrderRefundPsd :item="i"></OrderRefundPsd>
        </div>
    </div>
</template>
<script>
import OrderRefundPsd from '../components/OrderRefund/OrderRefundPsd'
export default {
    components:{
        OrderRefundPsd,
    },
    data(){
        return{
            // 退款数量，要在外部显示徽标
            num:null,
            // 退款列表
            list:[],
        }
    },
    created(){
        this.axios.get("/api/order/list?page=1&limit=20&type=-3").then(res=>{
            this.list=res.data.data;
            // console.log(this.list);
            this.num = this.list.length;
        })
    },
}
</script>
<style lang="less" scoped>
#refund{
    width: 100vw;
    // height: 100vh;
    box-sizing: border-box;
    background: #f5f5f5;
    .refund_list{
        background: #fff;
        padding: 0.3rem 0;
    }
}
</style>